$accent-clr: hsl(223, 85%, 58%);
$primary-clr: hsl(210, 16%, 97%);
$secondary-clr: hsl(225, 14%, 43%);
$red-dot: hsl(3, 93%, 65%);
$yellow-dot: hsl(44, 92%, 64%);
$green-dot: hsl(123, 53%, 59%);
$bg-clr-light: rgba(0, 0, 0, 0.08);
$bg-clr: rgba(0, 0, 0, 0.2);
$blur: blur(8px);
$h1-size: 15px;
$h2-p-size: 14px;
$transition: all 200ms ease-in-out;

@import "_mixins";
@import "header";
@import "weather-app";
@import "todo-app";
@import "main-apps";
@import "taskbar";
@import "recipe";

@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,300&display=swap");

*,
::after,
::before {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    font-family: "Lato", sans-serif;
    line-height: 1.5;
    width: 100%;
    height: 100vh;
    color: $primary-clr;
    background: url("../src/icons/Bubble-Background.svg") center center
        no-repeat fixed;
    background-size: cover;
    display: grid;
    place-content: center;
}

h1 {
    font-size: $h1-size;
}

h2 {
    font-size: $h2-p-size;
}

p {
    font-size: $h2-p-size;
}

img {
    width: 100%;
    max-width: 100%;
}

.wrapper {
    width: 95vw;
    height: 90vh;
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.5);
    background-color: $bg-clr;
    backdrop-filter: $blur;
    border-radius: 15px;
    position: relative;
    overflow: hidden;
}
