.apps {
    position: absolute;
    left: 50%;
    bottom: 3rem;
    transform: translateX(-50%);
    display: flex;
    gap: 2rem;
    background-color: $bg-clr-light;
    backdrop-filter: $blur;
    border-radius: 30px;
    padding: 0.5rem 5rem;
}

.app {
    font-size: 2rem;
    color: $secondary-clr;
    cursor: pointer;
    transition: $transition;

    &:hover {
        transform: translateY(-0.5rem);
        color: $primary-clr;
    }
    & svg {
        fill: #5e667d;
    }
}

@include media-md {
    .apps {
        font-size: 1.5rem;
        bottom: 0.5rem;
    }
}
